import { getCarryTicketList, getTicket } from '@/api/user.js'
import { xDeCode } from '@/utils/index'
export default {
  name: 'loginRegister',
  components: {
  },
  data () {
    return {
      isGet: false,
      isShow: true,
      goUse: false,
      ticketlist: {}
    }
  },
  methods: {
    handleGet() {
      getTicket({id: xDeCode(this.$route.query.id)})
        .then(res => {
          this.$vux.loading.hide()
          if (res.data.errorcode === 0) {
            // 处理数据
            // this.$vux.toast.text('领取成功！', 'middle')
            this.ticketlist.status = 0
            this.goUse = true
          } else {
            if (res.data.errorcode === 10039) {
              this.isGet = true
              this.isShow = false
            }
            if (res.data.errorcode === 10003) {
              // 登录后跳转至 优惠券
              localStorage.setItem('tickUrl', this.$route.fullPath)
            //   Cookies.set('tickUrl', this.$route.fullPath)
            }
            this.$vux.toast.text(res.data.errormsg, 'middle')
          }
        })
        .catch(err => {
          this.$vux.loading.hide()
          console.log(err)
          this.$vux.toast.text('网络异常！', 'middle')
        })
    }
  },
  created () {
    // 获取领取的优惠券id
    if(this.$route.query.id){
      let id = xDeCode(this.$route.query.id)
      getCarryTicketList({ids: id})
        .then(res => {
          this.$vux.loading.hide()
          if (res.data.errorcode === 0) {
            // 处理数据
            console.log(res)
            this.ticketlist = res.data.data.ticketlist[0]
          } else {
            this.$vux.toast.text(res.data.errormsg, 'middle')
          }
        })
        .catch(err => {
          this.$vux.loading.hide()
          console.log(err)
          this.$vux.toast.text('网络异常！', 'middle')
        })
    }
  }
}



